body {
  background: #ffffff;
  color: var(--color-gray-900);
  font-family: var(--font-family-sans);
}
@keyframes prism-flow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.bg-prism {
  background: var(--gradient-prism);
  background-size: 300% 300%;
  animation: prism-flow 8s ease-in-out infinite;
}
.text-prism {
  background: var(--gradient-prism-text);
  background-size: 200% auto;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: prism-flow 4s ease-in-out infinite;
}
.prism-border {
  background: var(--gradient-prism);
  background-size: 300% 300%;
  animation: prism-flow 8s ease-in-out infinite;
  padding: 1px;
}
.prism-border > * {
  background: white;
}
.prism-border-2 {
  background: var(--gradient-prism);
  background-size: 300% 300%;
  animation: prism-flow 8s ease-in-out infinite;
  padding: 2px;
}
.prism-border-2 > * {
  background: white;
}
.hover-prism {
  position: relative;
  overflow: hidden;
  transition: color 0.3s ease, border-color 0.3s ease;
}
.hover-prism::before {
  content: '';
  position: absolute;
  inset: 0;
  background: var(--gradient-prism);
  background-size: 300% 300%;
  animation: prism-flow 8s ease-in-out infinite;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 0;
}
.hover-prism:not(:disabled):hover::before {
  opacity: 1;
}
.hover-prism:not(:disabled):hover {
  color: white;
  border-color: transparent;
}
.hover-prism:not(:disabled):hover * {
  color: white;
}
.hover-prism-border {
  position: relative;
  transition: all 0.1s ease;
}
.hover-prism-border::before {
  content: '';
  position: absolute;
  inset: 0;
  padding: 2px;
  background: var(--gradient-prism);
  background-size: 300% 300%;
  -webkit-mask: linear-gradient(#fff 0 0) content-box,
    linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0;
  transition: opacity 0.1s ease;
}
.hover-prism-border:hover::before {
  opacity: 1;
  animation: prism-flow 8s ease-in-out infinite;
}
.hover-text-prism {
  transition: all 0.1s ease;
}
.hover-text-prism:hover {
  background: var(--gradient-prism-text);
  background-size: 200% auto;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: prism-flow 4s ease-in-out infinite;
}
@keyframes skeleton-wave {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
.animate-skeleton-wave {
  background: linear-gradient(
    90deg,
    #e4e4e7 0%,
    #f4f4f5 50%,
    #e4e4e7 100%
  );
  background-size: 200% 100%;
  animation: skeleton-wave 1.5s ease-in-out infinite;
}
